import type { Trailer, TripData } from '@/types';

const API_KEY = '0bcf49a90e765ca3d7ea8ba1ae25373142e374c556919aa3e5c41adf8b2ff220';
const TRAILERS_API_URL = 'https://get-trailers-and-trips-181509438418.us-central1.run.app';
const TRIP_DATA_API_URL = 'https://get-trip-data-181509438418.us-central1.run.app';

// Alternative CORS proxy (use if direct calls fail)
const CORS_PROXY = 'https://api.allorigins.win/raw?url=';
const USE_PROXY = false; // Toggle this if CORS issues persist

class TrailersService {
  private async makeAPICall(url: string, params?: Record<string, string>): Promise<any> {
    try {
      // Build URL with query parameters if provided
      const apiUrl = new URL(url);
      if (params) {
        Object.entries(params).forEach(([key, value]) => {
          apiUrl.searchParams.append(key, value);
        });
      }

      // Use proxy if needed
      const finalUrl = USE_PROXY ? `${CORS_PROXY}${encodeURIComponent(apiUrl.toString())}` : apiUrl.toString();
      
      console.log(`🌐 Making API call to: ${finalUrl}`);
      console.log(`🔑 Using API Key: ${API_KEY.substring(0, 10)}...`);

      const headers: Record<string, string> = {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      };

      // Only add API key if not using proxy
      if (!USE_PROXY) {
        headers['X-API-Key'] = API_KEY;
      }

      const response = await fetch(finalUrl, {
        method: 'GET',
        headers,
        // Try without mode first
      });

      console.log(`📡 Response status: ${response.status} ${response.statusText}`);
      console.log(`📡 Response headers:`, Object.fromEntries(response.headers.entries()));

      if (!response.ok) {
        const errorText = await response.text();
        console.error(`❌ API Error Response:`, errorText);
        throw new Error(`API call failed: ${response.status} ${response.statusText} - ${errorText}`);
      }

      const data = await response.json();
      console.log(`✅ API Response received. Type:`, typeof data, 'Length:', Array.isArray(data) ? data.length : 'N/A');
      console.log(`📊 Sample data:`, Array.isArray(data) ? data.slice(0, 2) : data);
      return data;

    } catch (error) {
      console.error(`❌ API call failed:`, error);
      
      // If direct call failed and we weren't using proxy, try with proxy
      if (!USE_PROXY && error instanceof TypeError && error.message.includes('Failed to fetch')) {
        console.log('🔄 Retrying with CORS proxy...');
        try {
          const apiUrl = new URL(url);
          if (params) {
            Object.entries(params).forEach(([key, value]) => {
              apiUrl.searchParams.append(key, value);
            });
          }
          
          // Add API key as query parameter for proxy
          apiUrl.searchParams.append('X-API-Key', API_KEY);
          const proxyUrl = `${CORS_PROXY}${encodeURIComponent(apiUrl.toString())}`;
          
          console.log(`🌐 Proxy API call to: ${proxyUrl}`);
          
          const proxyResponse = await fetch(proxyUrl, {
            method: 'GET',
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json',
            },
          });
          
          if (!proxyResponse.ok) {
            throw new Error(`Proxy API call failed: ${proxyResponse.status}`);
          }
          
          const proxyData = await proxyResponse.json();
          console.log(`✅ Proxy API Response received:`, proxyData);
          return proxyData;
        } catch (proxyError) {
          console.error(`❌ Proxy API call also failed:`, proxyError);
        }
      }
      
      throw error;
    }
  }

  async getTrailersAndTrips(): Promise<Trailer[]> {
    console.log('🚛 Fetching trailers and trips data...');
    
    try {
      const data = await this.makeAPICall(TRAILERS_API_URL);
      
      // Log the raw response to understand the structure
      console.log('📊 Raw trailers data structure:', typeof data, data);
      
      // Check if data is already in the correct format
      if (Array.isArray(data)) {
        console.log(`✅ Fetched ${data.length} trailers successfully`);
        return data as Trailer[];
      }
      
      // If data is wrapped in an object, extract the array
      if (data && data.trailers && Array.isArray(data.trailers)) {
        console.log(`✅ Fetched ${data.trailers.length} trailers successfully`);
        return data.trailers as Trailer[];
      }
      
      // Try other possible response structures
      if (data && typeof data === 'object') {
        // Check for other common array property names
        const possibleArrays = ['data', 'results', 'items', 'trailers_and_trips'];
        for (const prop of possibleArrays) {
          if (data[prop] && Array.isArray(data[prop])) {
            console.log(`✅ Found trailers in ${prop} property: ${data[prop].length} trailers`);
            return data[prop] as Trailer[];
          }
        }
      }
      
      // If data has a different structure, log it and throw error
      console.error('❌ Unexpected data structure:', data);
      throw new Error('Unexpected response format from trailers API. Check console for details.');
      
    } catch (error) {
      console.error('❌ Failed to fetch trailers:', error);
      throw new Error(`Failed to fetch trailers data: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  async getTripData(trailerId: string, tripId: string): Promise<TripData[]> {
    console.log(`🚚 Fetching trip data for trailer: ${trailerId}, trip: ${tripId}`);
    
    try {
      const data = await this.makeAPICall(TRIP_DATA_API_URL, {
        trailer_id: trailerId,
        trip_id: tripId
      });
      
      // Log the raw response to understand the structure
      console.log('📊 Raw trip data structure:', typeof data, data);
      
      // Check if data is already in the correct format
      if (Array.isArray(data)) {
        console.log(`✅ Fetched trip data with ${data.length} entries`);
        return data as TripData[];
      }
      
      // If data is wrapped in an object, extract the array
      if (data && data.trips && Array.isArray(data.trips)) {
        console.log(`✅ Fetched trip data with ${data.trips.length} entries`);
        return data.trips as TripData[];
      }
      
      // Try other possible response structures
      if (data && typeof data === 'object') {
        const possibleArrays = ['data', 'results', 'items', 'trip_data'];
        for (const prop of possibleArrays) {
          if (data[prop] && Array.isArray(data[prop])) {
            console.log(`✅ Found trip data in ${prop} property: ${data[prop].length} entries`);
            return data[prop] as TripData[];
          }
        }
      }
      
      // If data has a different structure, log it and throw error
      console.error('❌ Unexpected trip data structure:', data);
      throw new Error('Unexpected response format from trip data API. Check console for details.');
      
    } catch (error) {
      console.error('❌ Failed to fetch trip data:', error);
      throw new Error(`Failed to fetch trip data: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }
}

export const trailersService = new TrailersService(); 